<script>
import { BTabs, BTab, BBadge, BCardText, BFormGroup, BFormInput, BFormSelect, BButton } from 'bootstrap-vue'

 import ContactListTab from './ContactListTab.vue'
  import Vue from "vue"
  import Ripple from 'vue-ripple-directive'
  import { mapMutations } from 'vuex'
  import store from '@/store'
  export default {
   directives: {
    Ripple,
  },
  data(){
    return{
      id:null,
      userData:null,
      changed:false,
      staticData:null
    }
  },
  components: {
    BCardText,
    BTabs,
    BTab,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BButton,
    ContactListTab,
    BBadge
  },
    computed: {
    statusVariant() {
      const statusColor = {
        /* eslint-disable key-spacing */
        Active       : 'light-success',
        Inactive     : 'light-dark',
        /* eslint-enable key-spacing */
      }

      return status => statusColor[status]
    },
    },
  mounted(){
  this.id=this.$route.params.id;
  this.staticData=this.$store.getters.getUser(this.id);

  this.userData = JSON.parse(JSON.stringify(this.staticData));

  Vue.set(this.$route.meta.breadcrumb, 1, null)
  Vue.set(this.$route.meta.breadcrumb, 1, {text:this.userData.practice})
  Vue.set(this.$route.meta.breadcrumb, 1, null)
  Vue.set(this.$route.meta.breadcrumb, 1, {text:this.userData.practice})
  console.log(this.$route.meta.breadcrumb)
  },
  methods:{
    ...mapMutations(["saveUser"]),
      saveData(){
          this.saveUser(this.userData)
          this.$router.push({ name: 'practices-page' })
      }
  }
}
</script>

